<template>
	<view class="publish-area">
		<view class="btn-area">
			<text>发布</text>
		</view>
		<view class="content-area">
			<u--input placeholder="请输入标题" placeholderClass="title-input" border="bottom" clearable></u--input>
			<view class="editor-are">
				<robinEditor ref="robinEditor" class="richEditor" @cancel="hideEditor" @save="saveEditor" v-model="post.postDesc"
					:imageUploader="uploadImg" :muiltImage="true"></robinEditor>
			</view>
		</view>
	</view>
</template>

<script>
import robinEditor from '@/components/robin-editor/components/robin-editor/robin-editor.vue'
export default {
	components: { robinEditor },
	data() {
		return {
			
			post: {},
			
		}
	},
	onLoad() {
		
	},
	methods:{
		//富文本
		saveEditor(e) {
			this.postDesc = e.html;
		},
		async uploadImg(img, callback) {
		    //上传图片逻辑,将图片链接传给回调函数
			const [err, {data}] = await uni.uploadFile({
				url: this.$config.BASE_URL + '/wechat/upload/uploadFastDFS',
				filePath: img,
				name: 'file',
			});
			let resData = JSON.parse(data);
			if (resData && resData.code === 200) {
				callback(this.$config.IMG_URL + resData.data.imgUrl)
			}else{
			   uni.showToast({ title: "上传失败", icon: "none" })
			}
		},
		changeHtml(i){
		    //从远程获取数据赋值
		    this.html = i == 1 ? this.html : this.html2
		},
	}
}
</script>

<style lang="scss" scoped>
.publish-area{
	.btn-area{
		padding: 20rpx 30rpx 10rpx 0;
		display: flex;
		justify-content: flex-end;
		box-shadow: 0 10rpx 10rpx -4rpx #efefef;
		text{
			font-weight: 700;
		}
	}
	.content-area{
		padding: 40rpx 20rpx 0 20rpx;
		.editor-are{
			padding: 20rpx 0 0 10rpx;
		}
	}
}
</style>
<style>
.title-input{
	color: #B2B2B2;
	font-style: normal;
	font-size: 30rpx;
}
.ql-editor.ql-blank:before {
	color: #B2B2B2;
	font-style: normal;
	font-size: 30rpx;
}
</style>

